// export default Navbar;

export const NavbarTest = ({ theme, toggleTheme, open, setOpen }) => {
  return (
    <Nav open={open} setOpen={setOpen}>
      {NavbarData.map((data, i) => {
        return (
          <Links
            key={data.id}
            activeClass="active"
            to={data.id}
            spy={true}
            smooth={true}
            hashSpy={true}
            offset={-100}
            duration={500}
            isDynamic={true}
            ignoreCancelEvents={false}
            spyThrottle={100}
            className="icons"
            onClick={() => setOpen(false)}
          >
            {data.icon}
          </Links>
        );
      })}

      <a
        href="https://github.com/charyyev2000"
        title="Github"
        target="_blank"
        rel="noopener noreferrer"
        className="icons"
      >
        <Github />
      </a>
      <a
        href="https://linkedin.com/in/myrat-charyyev"
        title="LinkedIn"
        target="_blank"
        rel="noopener noreferrer"
        className="icons"
      >
        <Linkedin />
      </a>
      <Toggle theme={theme} toggleTheme={toggleTheme} />

      <NavBurger
        onClick={() => setOpen(!open)}
        open={open}
        setOpen={setOpen}
      ></NavBurger>
    </Nav>
  );
};

const Nav = styled.nav`
  position: fixed;
  bottom: 7vh;
  right: 5vw;
  cursor: pointer;
  height: ${(props) => (props.open ? "50px" : "340px")};
  width: 50px;
  z-index: 100;
  border-radius: 50px;
  transition: height 0.3s ease;
  display: flex;
  align-items: center;
  flex-direction: column;

  .icons {
    opacity: ${(props) => (props.open ? "0" : "1")};
    transition: opacity 0.3s ease;
    filter: drop-shadow(0 0 0.75rem black);
  }
`;

const NavBurger = styled.div`
  width: 50px;
  height: 50px;
  background: ${({ theme }) => theme.effect};
  transition: background 0.3s ease;
  border-radius: 50%;
  position: absolute;
  bottom: 0;

  .link {
    font-size: 60px;
    display: ${(props) => (props.open ? "block" : "none")};
    align-items: center;
    flex-direction: flex;
  }
`;
